/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  max-width: min(95%, 120rem);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: bold;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: min(95%, 80rem);
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.9rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 1.5rem;
  margin: 1rem 0 1rem 0;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.9rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 1.5rem;
  margin: 1rem 0 1rem 0;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.9rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 1.5rem;
  margin: 1rem 0 1rem 0;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(255, 0, 0);
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.9rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  margin: 1rem 0 1rem 0;
  border-radius: 1.5rem;
}
.boton-rojo-block:hover {
  background-color: #e60000;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.9rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 1.5rem;
  margin: 1rem 0 1rem 0;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-claro-block {
  background-color: #EFF396;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.9rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 1.5rem;
  margin: 1rem 0 1rem 0;
}
.boton-claro-block:hover {
  background-color: #ecf07f;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .texto-entrada h4 {
  color: #e1e1e1;
}
.dark-mode .contenido-propiedad .precio {
  color: #71B100;
}
.dark-mode .contenido-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .formulario {
  background-color: #1a1a1a;
  border: 1px solid #e1e1e1;
}
.dark-mode .formulario label, .dark-mode .formulario p, .dark-mode .formulario legend {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode .propiedades thead, .dark-mode .propiedades tbody {
  color: #FFFFFF;
}
.dark-mode .propiedades td {
  border-bottom: 0.1rem solid #FFFFFF;
}

.footer {
  padding: 2rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .oie {
  text-align: center;
  color: #FFFFFF;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.no-webp .inicio {
  background-image: url(/build/img/header.jpg);
}

.webp .inicio {
  background-image: url(/build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 2rem 0;
}
.header.inicio {
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  font-weight: 700;
  text-align: left;
  max-width: 60rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    gap: 2rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}

/*Menu responsive*/
.mobile-menu {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 2.5rem;
}
.mobile-menu:hover {
  cursor: pointer;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .derecha {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 4rem;
  }
}
.derecha i:hover {
  cursor: pointer;
}
.derecha .cerrar, .derecha #icon {
  color: #FFFFFF;
}
.derecha .cerrar:hover, .derecha #icon:hover {
  color: #71B100;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.iconos-nosotros {
  display: grid;
}
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos-nosotros .icono {
  text-align: center;
}
.iconos-nosotros .icono img {
  margin-top: 2rem;
  height: 10rem;
}

.contenido-anuncio {
  padding: 2rem;
}
.contenido-anuncio h3, .contenido-anuncio p {
  margin: 0;
}
.contenido-anuncio img {
  height: 4rem;
}

.precio {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
  margin: 1rem;
}

.anuncio {
  background-color: #e1e1e1;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 0;
  }
}
.iconos-caracteristicas {
  padding-left: 0;
  max-width: 40rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.iconos-caracteristicas li {
  list-style: none;
  display: flex;
  align-items: center;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(/build/img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(/build/img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  padding: 2rem;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 4rem;
}
.imagen-contacto p {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
  h3 {
    font-weight: bold;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  margin-top: 1rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  font-size: 2.4rem;
  font-weight: bold;
  color: #000000;
  margin: 0;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}

span {
  color: #E08709;
  font-weight: bold;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 4rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(./img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario {
  background-color: #cbd0b9;
  border-radius: 2rem;
  padding: 1rem;
  margin-top: 1rem;
}
.formulario p {
  font-size: 1.6rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario fieldset {
  border: none;
}
.formulario legend {
  font-weight: 700;
  font-size: 2.3rem;
  text-align: center;
}
.formulario label {
  font-weight: bold;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: none;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .sobre-nosotros h4 {
    margin: 0;
  }
}

h4 {
  margin: 2rem 0rem;
  font-weight: 900;
  font-size: 2.2rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  margin-bottom: 1rem;
  color: #FFFFFF;
  font-size: 1.9rem;
}
.alerta.error {
  background-color: rgb(255, 0, 0);
  border-radius: 0.5rem;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  text-align: center;
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
}
table.propiedades tbody {
  text-align: center;
}
table.propiedades tbody td {
  border-bottom: 0.1rem solid #000000;
  font-size: 1.8rem;
  margin-top: 1rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
  margin: 0 auto;
}
table.propiedades .acciones {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
table.propiedades .acciones i {
  color: #000000;
}
table.propiedades .acciones form .boton-rojo-block {
  width: 100%;
  height: 71%;
}

@media (min-width: 480px) {
  .acciones {
    display: block;
    justify-content: center;
  }
  .acciones i {
    color: #000000;
  }
  .acciones form .boton-rojo-block {
    width: 100%;
    height: 71%;
  }
}
.imagen-small {
  width: 25rem;
}